import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import NotFound from "@/ticketing/views/NotFound";
import VueMeta from "vue-meta";

import store from "@/store/store";
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";

import RSVPRoutes from "./rsvp-routes";
import ApplicationRoutes from "./application-routes";

Vue.use(Router);
Vue.use(VueMeta);
const router = new Router({
  // mode: "history",
  // base: "/events/05-2019/ticketing/",
  routes: [
    ...RSVPRoutes,
    ...ApplicationRoutes,
    {
      path: "/:eventSlug/tickets/purchase",
      name: "purchase-tickets",
      component: () =>
        import(/* webpackChunkName: "purchase" */ "@/ticketing/views/Purchase"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const { eventSlug } = routeTo.params

        try {
          store.dispatch("events/getEvent", eventSlug)
        } catch (err) {
          next({ name: "404" });
          return
        }

        next();
      }
    },
    {
      path: "/:eventSlug/tickets/:ticketOfferId/send",
      name: "manage-tickets",
      component: () =>
        import(/* webpackChunkName: "purchase" */ "@/ticketing/views/SendTickets"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const { eventSlug, ticketOfferId } = routeTo.params

        try {
          await Promise.all([
            store.dispatch("events/getEvent", eventSlug),
            store.dispatch("ticketOffers/getTicketOffer", ticketOfferId),
            store.dispatch("invites/getInvites", ticketOfferId),
            store.dispatch("invites/getNewInvite"),
          ])

          const { personId, invitedBy } = store.state.ticketOffers.ticketOffer
          if (invitedBy) next({
            name: 'ticket',
            params: routeTo.params
          })
          await store.dispatch("people/getPerson", personId)
        } catch (err) {
          next({ name: "404" });
          return
        }

        next();
      }
    },
    {
      path: "/:eventSlug/tickets/:ticketOfferId/profile",
      name: "update-profile",
      component: () =>
        import(/* webpackChunkName: "purchase" */ "@/ticketing/views/Profile"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const { eventSlug, ticketOfferId } = routeTo.params

        try {
          await Promise.all([
            store.dispatch("events/getEvent", eventSlug),
            store.dispatch("ticketOffers/getTicketOffer", ticketOfferId),
          ])

          const personId = store.state.ticketOffers.ticketOffer.personId
          await store.dispatch("people/getPerson", personId)

          // if (ProfileService.calcNeedsDetails(person)) {
          //   routeTo.params.needsDetails = true
          // }

        } catch (err) {
          next({ name: "404" });
          return
        }

        next();
      }
    },
    {
      path: "/:eventSlug/tickets/:ticketOfferId/ticket",
      name: "ticket",
      component: () =>
        import(/* webpackChunkName: "purchase" */ "@/ticketing/views/Ticket"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const { eventSlug, ticketOfferId } = routeTo.params

        try {
          await Promise.all([
            store.dispatch("events/getEvent", eventSlug),
            store.dispatch("ticketOffers/getTicketOffer", ticketOfferId),
          ])

          const personId = store.state.ticketOffers.ticketOffer.personId
          await store.dispatch("people/getPerson", personId)

          // TODO
          // If questions need to be filled out
          // redirect to profile
          // else
          // continue
          const person = store.state.people.person

          if (ProfileService.calcNeedsDetails(person)) {
            next({
              name: 'update-profile',
              params: routeTo.params
            })
          }

        } catch (err) {
          next({ name: "404" });
          return
        }

        next();
      }
    },

    {
      path: "/:eventSlug/ticket/:ticketOfferId/purchase",
      name: "ticket-offer-purchase",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketOfferPurchase"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const { eventSlug, ticketOfferId } = routeTo.params

        try {
          await Promise.all([
            store.dispatch("ticketOffers/getTicketOffer", ticketOfferId),
            store.dispatch("events/getEvent", eventSlug)
          ])
        } catch (err) {
          next({ name: "404" });
          return
        }

        const event = store.getters["events/event"];
        const ticketOffer = store.getters["ticketOffers/ticketOffer"]

        await store.dispatch("people/getPerson", ticketOffer.personId)

        if (ticketOffer.status === "surrendered") {
          next({
            name: "404",
            params: { message: "Ticket Offer Surrendered." }
          });
          return
        }

        if (ticketOffer.status === "purchased" || event.soldOut) {
          redirectToTicketOfferType(
            next,
            ticketOffer,
            eventSlug,
            ticketOfferId
          );
          return
        }

        const inviteData = Promise.all([
          TicketService.getInvites(ticketOfferId),
          TicketService.getNewInvite()
        ])

        const [invites, newInvite] = await inviteData

        routeTo.params.existingInvites = invites.data.invites;
        routeTo.params.newInvite = newInvite.data.invite;
        next();
      }
    },
    {
      path: "/:eventSlug/buy-tickets",
      name: "buy-tickets",
      component: () =>
        import(/* webpackChunkName: "apply" */ "@/ticketing/views/BuyTickets"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const { eventSlug } = routeTo.params

        try {
          await Promise.all([
            store.dispatch("people/getNewPerson"),
            store.dispatch("ticketOffers/getNewTicketOffer", eventSlug),
            store.dispatch("events/getEvent", eventSlug)
          ])
        } catch (err) {
          next({ name: "404" });
          return
        }

        const event = store.getters["events/event"];

        if (event.hasApplications) {
          next({ name: "apply", params: { eventSlug: eventSlug }});
          return
        }

        next();
      }
    },
    {
      path: "/:eventSlug/update-ticket-info/:id",
      name: "update-ticket-info",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketDetails"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(
          routeTo.params.eventSlug,
          routeTo.params.id
        )
          .then(ticketOfferRes => {
            const ticketOffer = ticketOfferRes.data.ticketOffer;
            routeTo.params.ticketOffer = ticketOffer;
            const event = ticketOfferRes.data.event;
            store.dispatch("layouts/setLayout", event.layout);
            routeTo.params.event = event;

            ProfileService.getProfile(ticketOffer.personId)
              .then(profileRes => {
                routeTo.params.person = profileRes.data.person;
                next();
              })
              .catch(() => {
                next({ name: "404" });
              });
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/offer/:id",
      name: "ticket-offer",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketOffer"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        await store.dispatch("ticketOffers/getTicketOffer", routeTo.params.id);
        const ticketOffer = store.getters["ticketOffers/ticketOffer"];
        await store.dispatch("events/getEvent", routeTo.params.eventSlug);
        const event = store.getters["events/event"];
        await store.dispatch("people/getPerson", ticketOffer.personId);
        const person = store.getters["people/person"];

        if (ticketOffer.kind !== "standard") {
          redirectToTicketOfferType(
            next,
            ticketOffer,
            routeTo.params.eventSlug,
            routeTo.params.id
          );
        } else if (
          !event.hasApplications &&
          ticketOffer.status !== "purchased"
        ) {
          next();
        } else {
          next();
        }
      }
    },
    {
      path: "/:eventSlug/vip-invites/:id",
      name: "vip-ticket-offer",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/VipInvite"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.id)
          .then(() => {
            const ticketOffer = store.getters["ticketOffers/ticketOffer"];
            if (ticketOffer.kind !== "vip") {
              redirectToTicketOfferType(
                next,
                ticketOffer,
                routeTo.params.eventSlug,
                routeTo.params.id
              );
            } else {
              store
                .dispatch(
                  "people/getPerson",
                  store.getters["ticketOffers/ticketOffer"].personId
                )
                .then(() => {
                  store
                    .dispatch("events/getEvent", routeTo.params.eventSlug)
                    .then(() => {
                      const event = store.getters["events/event"];
                      const person = store.getters["people/person"];
                      const ticketOffer =
                        store.getters["ticketOffers/ticketOffer"];

                      next();
                    })
                    .catch(() => {
                      next({ name: "404" });
                    });
                });
            }
          });
      }
    },
    {
      path: "/:eventSlug/partners/registration/:id",
      name: "partner-registration",
      component: () =>
        import(/* webpackChunkName: "partner-registration" */ "@/ticketing/views/PartnerRegistration"),
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.id)
          .then(() => {
            const ticketOffer = store.getters["ticketOffers/ticketOffer"];
            if (ticketOffer.kind !== "partner") {
              redirectToTicketOfferType(
                next,
                ticketOffer,
                routeTo.params.eventSlug,
                routeTo.params.id
              );
            } else {
              store.dispatch(
                "people/getPerson",
                store.getters["ticketOffers/ticketOffer"].personId
              );
              store
                .dispatch("events/getEvent", routeTo.params.eventSlug)
                .then(() => next());
            }
          });
      }
    },
    {
      path: "/:eventSlug/:orgName/registration",
      name: "partner-link-registration",
      component: () =>
        import(/* webpackChunkName: "partner-registration" */ "@/ticketing/views/PartnerRegistration"),
      async beforeEnter(routeTo, routeFrom, next) {
        await store.dispatch("organisations/getOrganisation", routeTo.params.orgName)
        await store.dispatch("events/getEvent", routeTo.params.eventSlug)
        await store.dispatch("ticketOffers/getNewTicketOffer", routeTo.params.eventSlug);
        await store.dispatch("people/getNewPerson")
        next()
      }
    },
    {
      path: "/404",
      name: "404",
      component: NotFound,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        store.commit("setLoading", false, { root: true });
        next()
      }
    },
    {
      path: "*",
      redirect: { name: "404" }
    }
  ]
});

router.beforeEach((routeTo, routeFrom, next) => {
  NProgress.start();
  next();
});

router.afterEach(() => {
  NProgress.done();
});

function redirectToTicketOfferType(next, ticketOffer, eventSlug, id = null) {
  next({
    name: getLocation(ticketOffer.kind),
    params: id ? { id, eventSlug } : { eventSlug }
  });
}

function getLocation(kind) {
  return {
    standard: "ticket-offer",
    public: "buy-tickets",
    partner: "partner-registration",
    vip: "vip-ticket-offer"
  }[kind];
}

export default router;
