import store from "@/store/store";
import TicketService from "@/services/TicketService";

export default  [
  { path: '/te-tiriti-technology-test/rsvp', redirect: '/a-tiritiri-based-approach-to-tech/rsvp' },
  {
    path: "/:eventSlug/rsvp/closed",
    name: "rsvps-closed",
    component: () =>
      import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPClosed"),
    props: true,
    beforeEnter(routeTo, routeFrom, next) {
      TicketService.getEvent(routeTo.params.eventSlug)
        .then(res => {
          routeTo.params.event = res.data.event;
          next();
        })
        .catch(() => {
          next({ name: "404" });
        });
    }
  },
  {
    path: "/:eventSlug/rsvp",
    name: "rsvp-new-person",
    component: () =>
      import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPs"),
    props: true,
    async beforeEnter(routeTo, routeFrom, next) {
      try {
        await Promise.all([
          store.dispatch("events/getEvent", routeTo.params.eventSlug),
          store.dispatch("people/getNewPerson"),
          store.dispatch("rsvps/getNewRSVP")
        ]);
        next();
      } catch (e) {
        next({ name: "404" });
      }
    }
  },
  {
    path: "/:eventSlug/rsvp/:personId",
    name: "rsvp",
    component: () =>
      import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPs"),
    props: true,
    async beforeEnter(routeTo, routeFrom, next) {
      const eventSlug = routeTo.params.eventSlug;
      const personId = routeTo.params.personId;
      await Promise.all([
        store.dispatch("events/getEvent", eventSlug),
        store.dispatch("people/getPerson", personId)
      ]);
      const event = store.getters["events/event"];
      if (event && !event.hasRsvps) {
        next({
          name: "404",
          params: { message: "This event doesn't have RSVP's enabled." }
        });
      } else {
        await store.dispatch("rsvps/getRSVP", { eventSlug, personId });
        next();
      }
    }
  },
]
